.founders{background-image:url(./img/Bg.png);display:grid;height:67rem;background-size:cover;grid-template-rows:repeat(6, 1fr);grid-template-columns:.5fr 1fr}.founders-title{grid-row-start:2;margin:0;font-family:Montserrat Alternates,serif;font-size:48px;font-weight:500;line-height:59px;color:#fff;background-color:#bf1616;width:509px;height:99px;border-radius:5px;text-align:center;padding-top:1rem}.founders-img{grid-row:3/6;justify-self:center;display:flex;flex-wrap:wrap;width:15.5rem}.founders-img p{position:relative;font-family:Montserrat,serif;font-size:16px;font-weight:500;line-height:20px;width:248px;height:97px;color:#fff;background-color:#bf1616;padding-left:2.5em;padding-top:1em;margin:0}.founders-img p::before{content:"“";position:absolute;top:25px;left:15px;font-size:48px;font-family:"Montserrat",serif}.founders-container{position:relative;display:grid;justify-items:center;justify-self:center;grid-template-rows:1fr 1fr .5fr 1fr 1fr;grid-template-columns:1fr .1fr;background-color:#fffbf6;height:50rem;padding:1.5rem 0 3rem 1.5rem}.founders-container--ornament{grid-row:1/3;grid-column:2/5;justify-self:end}.founders-container--name{font-family:"Mariupol",sans-serif;font-size:24px;font-weight:700;line-height:23px;color:#bf1616;margin:0;align-self:center;width:28.5rem}.founders-container--title{font-family:"Montserrat",sans-serif;font-size:20px;font-weight:500;line-height:24px;color:#bf1616;margin:0;align-self:start;width:28.5rem}.founders-container--info{font-family:"Montserrat",sans-serif;font-size:18px;font-weight:400;line-height:22px;grid-row-start:4;grid-column-start:1;margin:0;align-self:center;width:28.5rem}.founders-container--text{grid-column-start:1;grid-row-start:5;font-family:"Montserrat",sans-serif;font-size:18px;font-weight:400;line-height:22px;margin:0;align-self:center;width:28.5rem}.founders-carousel{display:flex;scroll-behavior:smooth;position:relative;grid-row:2/6;grid-column:2/3;width:36.8rem;margin:0 auto}.carousel{height:49.125rem}.carousel-root{grid-row:2/5}